<template lang="html">
  <div id="annotate">
      <div class="annotate-top-gap"></div>
      <div id="annotate-container">
          <div id="annotate-container-content">
              <div style="width:100%;height:60px;color:#393939; text-align:center;line-height:60px;font-size:20px;">{{currentDatabase}}</div>
              <div id="process-bar">
                  <el-progress :text-inside="true" :stroke-width="26"   :percentage="percentage"></el-progress>
              </div>
              <div id="annotation-text">
                  <div style="width:100%;height:20px;"> </div>
                  <div class="annotation-card" v-for="(text, index) in textArr" v-bind:key='index'>
                      <div class="annotation-card-header" v-bind:class="{ 'header-highlight' : labelResult[index].length > 0}">{{index}}</div>
                      <div class="annotation-card-content" >
                          <div class="annotation-card-text">
                              From the beginning of the movie, it gives the feeling the director is trying to portray something, what I mean to say that instead of the story dictating the style in which the movie should be made, he has gone in the opposite way, he had a type of move that he wanted to make, and wrote a story to suite it. And he has failed in it very badly. I guess he was trying to make a stylish movie. Any way I think this movie is a total waste of time and effort. In the credit of the director, he knows the media that he is working with, what I am trying to say is I have seen worst movies than this. Here at least the director knows to maintain the continuity in the movie. And the actors also have given a decent performance.
                          </div>
                          <div class="annotation-card-options">
                              <div class="annotation-card-options-button" v-for="(value, key) in labels" v-bind:key='key'>
                                      <el-button v-for='(val, i) in value' v-bind:key='i' type="info" @click.native="selectAttr(val, index)">{{val}}</el-button>
                             </div>

                              <div class="user-input">
                                  <el-input placeholder="Issue"  size="medium" v-model="issue"></el-input>
                              </div>
                              <div class="user-input">
                                  <el-input placeholder="Comment" size="medium" v-model="comment"></el-input>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
              <div id="annotate-submit">
                  <el-button type="primary" @click="getNext()">Next</el-button>
              </div>
              <div style="width:100%;height:20px;"></div>
          </div>
          <div class="annotate-container-gap"></div>
      </div>
      <div class="annotate-top-gap"></div>
  </div>
</template>

<script>
var textarray = [1,2,3,4, 5, 5, 6, 7, 7,5, 6, 7, 7]
export default {
    name: 'Annotate',
    data: function() {
        return {
            textArr: textarray,
            labelResult: this.getlabelResult(),
            // info: "success"
            issue: '',
            comment: '',
            currentDatabase: this.GLOBAL.currentDatabase,
        }
    },
    mounted: function() {
        // console.log(this.GLOBAL.currentDatabase)
    },
    computed: {
        labels: function() {
            if(this.currentDatabase == 'Crime') {
                return this.GLOBAL.gloablOptions['Crime']
            }else {
                return this.GLOBAL.gloablOptions['Crime']
            }
            // return {}
        },
        percentage: function() {
            var total = this.labelResult.length
            var sum = 0
            for(var i = 0; i < this.labelResult.length; i++) {
                if(this.labelResult[i].length > 0) sum++
            }
            return parseInt((sum * 100 / total).toFixed(0))
        }
    },
    methods: {
        getlabelResult: function() {
            var res = []
            for(var i = 0; i < textarray.length; i++) {
                res.push([])
            }
            return res
        },
        selectAttr: function (attr, index) {
            // console.log(event.target.tagName)
            var ele = event.target
            if(event.target.tagName == 'SPAN') {
                ele = ele.parentNode
            }

            if(ele.className.split(' ').includes('button-select')) {
                ele.classList.remove('button-select')
            }else {
                ele.classList.add('button-select')
            }
            if(this.labelResult[index].includes(attr)) {
                var len = this.labelResult[index].length
                for( var i = 0; i < len; i++){
                   if (this.labelResult[index][i] === attr) {
                     this.labelResult[index].splice(i, 1);
                     break
                   }
                }
            }else {
                this.labelResult[index].push(attr)
            }
            // console.log(this.labelResult)

        },
        changeDatabase: function() {
            this.currentDatabase = this.GLOBAL.currentDatabase
            // console.log(this.GLOBAL.currentDatabase)
        },
        getNext: function() {
            this.labelResult = this.getlabelResult()
            var button = document.getElementsByTagName('button')
            for(var i = 0; i < button.length; i++) {
                if(button[i].className.split(' ').includes('button-select')) {
                    button[i].classList.remove('button-select')
                }
            }
            document.getElementById('annotation-text').scrollTop = 0
        }

    },
    components: {

    },
}
</script>

