        <div style="width:100%;height:20%;"></div>
        <form @submit.prevent="handleSubmit">
            <div id="login-card">
                <div id="login-header">
                    <h3 class="mb-1">Login Here</h3>
                    <p class="mb-0">Welcome back, please login to your account.</p>
                </div>
                <div class="login-form">
                    <input type="text" placeholder="Username" v-model="username" name="username" class="form-control" :class="{ 'is-invalid': submitted && !username }" />
                </div>
                <div class="login-form">
                    <input type="password" placeholder="password"  v-model="password" name="password" class="form-control" :class="{ 'is-invalid': submitted && !password }" />
                </div>
                <div class="login-submit">
                    <button class="login-button" :disabled="loading">Login</button>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import { userService } from "../_services/user.service.js"
import router from "../router/index.ts"


export default {
  data() {
    return {
      username: "",
      password: "",
      submitted: false,
      loading: false,
      returnUrl: "",
      error: ""
    };
  },
  created() {
    // reset login status
    userService.logout();
    // get return url from route parameters or default to '/'
    this.returnUrl = this.$route.query.returnUrl || "/";
  },
  methods: {
    handleSubmit(e) {
      this.submitted = true;
      const { username, password } = this;

      // stop here if form is invalid
      if (!(username && password)) {
        return;
      }

      this.loading = true;
      userService.login(username, password).then(
        user => router.push(this.returnUrl),
        error => {
          this.error = error;
          this.loading = false;
        }
      );
    }
  }
};
</script>
